:root {
  --color-general: #d41443;
  --color-second: #ffa5ba;
  --btn-white: #fff;
  --btn-white-hover: #ecebeb;
  --color-title: #000;
  --color-text: #907e82;
  --anim-link: 250ms cubic-bezier(.1, .7, 1, .1);
  --font-general: "DM Sans";
  --font-second: "Titan One";
  --gap: 32px;
  --top: 20px;
  --card-number: 3;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

*, :before, :after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p, li, ul, ol {
  margin: 0;
  padding: 0;
}

input {
  border: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

button {
  border: none;
}

ul, ol {
  list-style: none;
}

a {
  text-decoration: none;
}

body {
  font-family: var(--font-general);
  color: #fff;
  margin: 0;
  font-size: 16px;
}

body.no-scroll {
  overflow: hidden;
}

.container {
  max-width: 320px;
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

.btn-other {
  text-align: center;
  border-radius: 22px;
  padding: 13px 37px;
  font-size: 16px;
  line-height: 1.31;
}

.btn-hero {
  text-align: center;
  border-radius: 20px;
  padding: 11px 24px;
  font-size: 14px;
  line-height: 1.29;
}

.btn-white {
  color: var(--color-general);
  background-color: var(--btn-white);
}

.btn-white:hover, .btn-white:focus {
  background-color: #ecebeb;
}

.btn-red {
  color: var(--btn-white);
  background-color: var(--color-general);
}

.svg-vector {
  width: 10px;
  height: 16px;
  fill: var(--color-general);
  transform: rotate(90deg);
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
  transition: opacity var(--anim), visability var(--anim);
  background: rgba(0, 0, 0, .2);
  padding: 15px;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  width: 100%;
  height: 100%;
  z-index: 1000;
  background: #e17992;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  padding: 15px;
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 768px) {
  .modal {
    width: 600px;
    height: 450px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    width: 900px;
    height: 600px;
  }
}

.modal__btn {
  fill: var(--btn-white);
  cursor: pointer;
  transition: fill var(--anim);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 8px;
  padding: 0;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

.modal__btn:hover, .modal__btn:focus + svg {
  fill: var(--btn-white-hover);
  cursor: pointer;
}

.map {
  width: 100%;
  height: 100%;
}

.modal__btn {
  width: 30px;
  height: 30px;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  box-shadow: 0 30px 90px rgba(0, 0, 0, .4);
}

.modal__btn-close {
  content: "";
  width: 20px;
  height: 1px;
  background-color: #000;
  transition: transform .25s linear;
  display: block;
  position: relative;
  transform: rotate(45deg);
}

.modal__btn-close:after {
  content: "";
  width: 20px;
  height: 1px;
  background-color: #000;
  transition: transform .25s linear;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotate(90deg);
}

.modal__btn:hover .modal__btn-close {
  transform: rotate(0deg);
}

.modal__btn:hover .modal__btn-close:after {
  top: 10px;
  transform: rotate(0deg);
}

.section-title {
  font-family: var(--font-second);
  color: var(--color-general);
  text-transform: uppercase;
  font-size: 34px;
}

@media screen and (min-width: 768px) {
  .section-title {
    font-size: 48px;
  }
}

@media screen and (min-width: 1200px) {
  .section-title {
    font-size: 58px;
  }
}

.section-label {
  font-family: var(--font-second);
  color: var(--color-second);
  text-transform: uppercase;
  font-size: 18px;
}

@media screen and (min-width: 768px) {
  .section-label {
    font-size: 26px;
  }
}

@media screen and (min-width: 1200px) {
  .section-label {
    font-size: 30px;
  }
}

.cardset {
  padding: 0;
}

@media screen and (min-width: 768px) {
  .cardset {
    margin-left: calc(-1 * var(--gap) );
    margin-top: calc(-1 * var(--top) );
    flex-wrap: wrap;
    justify-content: center;
    display: flex;
  }
}

.cardset__item {
  padding: 40px;
}

@media screen and (min-width: 768px) {
  .cardset__item {
    flex-basis: calc(100% / var(--card-number)  - var(--gap) );
    margin-left: var(--gap);
    margin-top: var(--top);
    padding: 30px;
  }
}

@media screen and (min-width: 1200px) {
  .cardset__item {
    padding: 60px;
  }
}

.header {
