}

.header__close-btn {
  width: 20px;
  height: 1px;
  background-color: #fff;
  display: block;
  position: relative;
  transform: rotate(45deg);
}

.header__close-btn:after {
  content: "";
  width: 20px;
  height: 1px;
  background-color: #fff;
  display: block;
  position: absolute;
  left: 0;
  transform: rotate(90deg);
}

.mobil-menu {
  width: 254px;
  height: 100%;
  z-index: 160;
