.btn-top {
  width: 55px;
  height: 55px;
  z-index: 500;
  cursor: pointer;
  background: rgba(225, 121, 145, .498);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  bottom: 100px;
  right: 30px;
  box-shadow: 0 19px 38px rgba(0, 0, 0, .3), 0 15px 12px rgba(0, 0, 0, .22);
}

@media screen and (min-width: 768px) {
  .btn-top {
    width: 65px;
    height: 65px;
  }
}

@media screen and (min-width: 1200px) {
  .btn-top {
    width: 80px;
    height: 80px;
  }
}

.btn-top__svg {
  width: 40px;
  height: 40px;
  transition: transform .25s linear;
}

@media screen and (min-width: 768px) {
  .btn-top__svg {
    width: 50px;
    height: 50px;
  }
}

@media screen and (min-width: 1200px) {
  .btn-top__svg {
    width: 65px;
    height: 65px;
  }
}

.btn-top__svg:hover, .btn-top__svg:focus {
  animation-name: rotate;
  animation-duration: .5s;
}

@keyframes rotate {
  33% {
    transform: rotate(35deg);
  }

  66% {
    transform: rotate(-35deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

.hero {
